<template>
  <div class="modal" tabindex="-1" :key="mId" :id="mId">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header d-flex justify-content-between align-items-start">
          <div class="col">
            <slot name="header" :hide="hide">
              <h5 class="modal-title">Modal title</h5>
            </slot>
          </div>
          <div class="col-auto">
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
          </div>
        </div>
        <div class="modal-body">
          <slot>#Modal Body</slot>
        </div>
        <div class="modal-footer">
          <slot name="footer"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import _ from './../../helpers';

export default {
  name: "Modal",
  props: {
    backdrop: {type: [Boolean, String], default: 'static'},
    keyboard: {type: Boolean, default: false},
    focus: {type: Boolean, default: true},
  },
  data() {
    return {mId: null, mModal: null}
  },
  methods: {
    getId() {
      return _.md5(_.mathRand('modal'));
    },
    show() {
      this.mModal.show();
    },
    hide() {
      this.mModal.hide();
    },
    dispose() {
      this.mModal.dispose();
    },
    instance() {
      return bootstrap.Modal.getInstance(document.getElementById(this.mId));
    },
  },
  mounted() {
    this.mId = this.getId();
    this.mModal = new bootstrap.Modal(document.getElementById(this.mId), {
      keyboard: this.keyboard, backdrop: this.backdrop, focus: this.focus
    });

    document.getElementById(this.mId).addEventListener('show.bs.modal', e => {
      this.$emit('onShow', e);
    });
    document.getElementById(this.mId).addEventListener('shown.bs.modal', e => {
      this.$emit('onShown', e);
    });
    document.getElementById(this.mId).addEventListener('hide.bs.modal', e => {
      this.$emit('onHide', e);
    });
    document.getElementById(this.mId).addEventListener('hidden.bs.modal', e => {
      this.$emit('onHidden', e);
    });
    document.getElementById(this.mId).addEventListener('hidePrevented.bs.modal', e => {
      this.$emit('onHidePrevented', e);
    });
  }
};
</script>

<style scoped></style>
