<template>
  <button type="button" class="btn btn-outline-secondary" @click="show">
    <slot>#ModalButton</slot>
  </button>
</template>

<script>

export default {
  name: "ModalButton",
  components: {},
  data() {
    return {modal: false}
  },
  methods: {
    show() {
      this.modal = true;
      this.$refs.myModal.show();
    },
    hidden() {
      this.$emit('hidden');
      this.modal = false;
    }
  }
};
</script>

